/* Soft Skills  */

.Soft_Skills{
  display: flex;
  gap: 33px;
  flex-direction: column;
  /* align-items: center; */

}

.Soft_Skills > div{
  border: 1px solid;
  border-radius: 100px;
  display: flex;
  flex-direction: column;
  width: 74%;
  padding: 12px;
  

}
.SoftSkill_icon{
  font-size: 35px;
  margin: auto;
  
  

}


.Soft_Skills div{
  transition: .3s ease;  

}

.Soft_Skills div:hover{
  transform: scale(1.1);

  border: 1px solid #00abf0;


}

.about_Top button{
    padding: 13px 4%;
    position: relative;
    font-size: 18px;
    border: none;
    border-radius: 15px;
    /* background-color:#00abf0; */
    color:#00abf0;
    font-weight: 540;
    cursor: pointer;
    box-sizing: border-box;
    transition: color 0.5s,trasform 0.2s,background-color 0.5s;
    outline: none;
    border: 2px solid #00abf0;
}

.about_Top > div:nth-of-type(2) > p:nth-of-type(1){
    font-size: 23px;
    color: #00abf0;
    font-weight: 500;
    margin-bottom: 2%;
}

.about_Top > div:nth-of-type(2) > p:nth-of-type(2){
    font-size: 17px;
  margin-bottom: 4%;
  width: 90%;
  
    
}

p{
  margin-bottom: 2%;
}
  
.about_Top button:hover
  {
    background-color: #00abf0;
    box-shadow: none;
    color: white;
    font-weight: 400;
  
  }

  /* for border try rotate  */
  

  .Image_Div{
    
    position: relative;
    width: 280px;
    height: 370px;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    border-radius: 20px;
  }
  .Image_Div::before{
    content: '';
    position: absolute;
    width: 150px;
    height: 140%;
    background: #00abf0;
    animation: animate 5s linear infinite;

  }

.Image_Div::after{
    content: '';
    position: absolute;
    inset: 2px;
    background: #081b29;
    border-radius: 18px;
}


.White_Image_Div{
    
  position: relative;
  width: 270px;
  height: 355px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  border-radius: 20px;
}
.White_Image_Div::before{
  content: '';
  position: absolute;
  width: 155px;
  height: 140%;
  background: black;
  animation: animate 5s linear infinite;

}

.White_Image_Div::after{
  content: '';
  position: absolute;
  inset: 2px;
  background: rgb(226, 221, 221);
  border-radius: 20px;
}


@keyframes animate {
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}

  .Image_Div > img {
    width: 260px;
    /* height: 320px; */
    border-radius: 3%;
    position: relative;
    z-index: 1;
    border: 2px solid #00abf0;;
} 

.White_Image_Div > img {
  width: 260px;
  /* height: 320px; */
  border-radius: 3%;
  position: relative;
  z-index: 1;
  border: 2px solid black;;
} 


/* Resposiveness  */

@media (max-width:800px){
