.nav_Top {
    position: fixed;
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    z-index: 100;
    padding: 13px 10%;
    top: 0px;
    background-color: #081b29;
    /* border: 1px solid; */

}

.white_nav_Top{
    position: fixed;
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    z-index: 100;
    padding: 13px 10%;
    top: 0px;
    background-color: #081b29;
    color: white;
}


.nav_Item {
    font-size: 18px;
    font-weight: 500;
}




.nav_Item ul {
    float: right;
    margin-right: 20px;
}

.nav_Item ul li {
    display: inline-block;
    line-height: 70px;
    margin: 0 23px;
}

.checkbtn {
    float: right;
    line-height: 89px;
    align-items: center;
    margin-top: 20px;
    margin-right: 40px;
    display: none;

   }

#check {
    display: none;
}


a:hover {
    color: #00abf0;
    transform: .5s;
}



@media (max-width :820px) {
    .checkbtn {
        display: block;
        margin-top: 7px;
        margin-right: 10px;
    }

    .nav_Top{
        padding-bottom: 0px;
        position: fixed;

    }

    ul {
        position: fixed;
        width: 50%;
        height: 100%;
        top: 0;
        text-align: center;
        left: -100%;
        padding-top: 3%;
        transition: all .6s;
        background-color: #081b29;
        
    }

    .nav_Item ul li{
        display: block;
        /* margin-top: 5px; */
    }
    .nav_Item ul li a{  
        font-size: 18px;
    }
    a:hover{
        color: #00abf0;
    }
    #check:checked ~ ul{
        left: 0;
    }
    
}