

.Top_Div{
    
    padding:12% 0% 0% 2%;
    display: flex;
    justify-content: space-between;
}

.Top_Div h1{
    font-size: 52px;
    font-weight: 550;
    padding: 0;
    margin: 0;
}



.Top_Div span{
    color:#00abf0;
}
.Top_Div  h3, span{
font-size: 28px;
margin: 0px;
margin-bottom: -4px;
padding: 0px;
font-weight: 400;

}




.Top_Div > div:nth-of-type(2){
    width: 50%;
    margin-top: 30px;
    margin-left: -10%;
}

.Home_icon_Div{
    margin-top: 4%;
    display: flex;
    width: 47%;
    margin: auto;
    justify-content: space-between;
    
}

.Home_icon_Div a{
    font-size: 40px;
}

.Home_icon_Div a{
    transition: transform 0.7s ease;

}

.Home_icon_Div  a:hover{
    
    transform:  rotate(-360deg);
}



 .bulbImageDiv{
    margin-top: 25%;
   text-align: center;

 }

.bulbImageDiv > img{
    cursor: pointer;
    width: 190px;

    

    
}
.image_Div img{
     margin-top: -10%;
    width: 440px;
    float: left;
    z-index: 1;
}


.Top_Div button{
    padding: 13px 5%;
    font-size: 18px;
    border: none;
    border-radius: 15px;
    background-color:#00abf0;
    color: black;
    font-weight: 540; 
    margin-bottom: 6%;
}




/* Resposiveness  */


@media (max-width :800px){
    
    .Top_Div{
        flex-wrap: wrap-reverse ;
     
    }
    .Top_Div > div:nth-of-type(1){
        display: none;
    }


    .Top_Div > div:nth-of-type(2){
        text-align: center;
        width: 100%;
        display: block;
        justify-content: center;
        margin-bottom: 5%;
        margin-left: 2%;
    }
    
    .image_Div img{
    
        text-align: center;
        width: 260px;
        margin: auto;
    }
    .image_Div{
        /* border: 1px solid; */
        margin: 18% auto;
        
    }
    .Home_icon_Div{
        margin: auto;
    }

    .Home_icon_Div{
        margin-top: 4%;
        display: flex;
        width: 70%;
       
        justify-content: space-between;
        
    }
    .Home_icon_Div a{
        font-size: 34px;
    }
   
}
