#contact{
    padding: 7% 10% 5% 10%;
    text-align: left;
}

#contact > h1 {
    padding: 0px;
    margin: 0px;
}

#contact > p {
    padding: 0px;
    padding-bottom: 30px;
    margin: 0px;
}

.main_Contact{
    display: flex;

}

.start_Div{
    width: 37%;
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding: 10px;
   
}




.details_Div{
    display: flex;
    justify-content: space-between;
    border: 1px solid;
    align-items: center;
    padding: 23px 14px;
    width: 360px;
    border-radius: 6px;


    
}
.details_Div > div > p:nth-of-type(1){
    font-weight: 600;
    margin-bottom: 4%;
}




.icon_Contact{
    font-size: 2.1rem;
   
    width: 20%;
    padding: 5px;
    align-items: center;
    
}
.main_Contact > div:nth-of-type(2){
    display: flex;
    justify-content: center;
    align-items: center;
}

.start_Div .details_Div{
    transition: transform 0.3s ease; 
 }


.start_Div .details_Div:hover{
   transform: scale(1.1); 
}

/* Responsiveness  */

@media (max-width:800px){
    .main_Contact >div> img{
        display: none;
    }
}

@media (max-width : 500px){
    .details_Div{
        width: 320%;
    }
    .details_Div > div > p{
        font-size: 13px;
    }
    .icon_Contact{
        font-size: 1.6rem;
        
        
        
        
    }

}