.App {
  text-align: center;
  font-family: 'Quicksand', sans-serif;
  background-color: #081b29; 
  color: white ;
}

.whiteApp{
  text-align: center;
  font-family: 'Quicksand', sans-serif;

  background-color: rgb(226, 221, 221); 
  color: black;
}


html {
  scroll-behavior: smooth;
}
.scroll {
	width: 60px;
	height: 60px;
	border: 2px solid #00abf0;
	border-radius: 50%;
	position: relative;
  margin-left: 90%;
  margin-top: -5%;
	animation: up 1.5s infinite;
	-webkit-animation: up 1.5s infinite;
	&::before {
		content: '';
		position: absolute;
		top: 20px;
		left: 18px;
		width: 18px;
		height: 18px;
		border-top: 2px solid #00abf0;
  	border-right: 2px solid #00abf0;
		transform: rotate(-45deg);
	}
}

@keyframes up {
	0% {
		transform: translate(0);
	}
	20% {
		transform: translateY(-15px);
	}
	40% {
		transform: translate(0);
	}
}

@-webkit-keyframes up {
	0% {
		transform: translate(0);
	}
	20% {
		transform: translateY(-15px);
	}
	40% {
		transform: translate(0);
	}
}


@media (max-width:720px) {
	.scroll{
		display: none;
	}
}

