.project-card {
    display: flex;
    gap: 40px;
    margin-bottom: 7%;
    /* align-items: center; */
    justify-content: center;
    /* border: 1px solid; */
    margin-top: 2%;
}

.projectImages {
    width: 1000px;
    height: 360px;
    border-radius: 20px;
    border: 2px solid #081b29;
    padding: 4px;

}

.project-title {
    margin-bottom: 3%;
}



.project-tech-stack {
    margin-top: 4%;
    font-size: 19px;
}

.project-card>div:nth-of-type(2) {
    text-align: left;
    width: 80%;
    /* border: 1px solid; */


}

.projectSkills img {
    width: 50px;
    border-radius: 5px;


}

.projectSkills {
    margin-top: 2%;
    display: flex;
    gap: 40px;
}

.ProjectButton>a>button {

    padding: 10px 20px 10px 20px;
    font-size: 19px;
    border: 2px solid #00abf0;
    border-radius: 10px;
    /* background-color:#00abf0; */
    cursor: pointer;
    color: #00abf0;
    /* font-weight: 540; */

}


.ProjectButton {
    display: flex;
    gap: 23%;
    margin-top: 7%;

}

.ProjectButton>a {
    text-decoration: none;
    background: none;
    color: #00abf0;

}

.ProjectButton button:hover {
    background-color: #00abf0;
    box-shadow: none;
    color: white;
    font-weight: 400;

}

.ProjectButton button a:hover {

    color: white;

}

.project-card img{
    cursor: pointer;
    transition: .4s ease;

}


.project-card img:hover{
   transform: scale(1.05);
}

/* Responsiveness  */

@media (max-width:800px) {
    #projects h1 {
        padding-top: 10%;

    }

    .project-card {
        flex-wrap: wrap;
    }

    .projectImages {
        width: auto;
        height: 130%;
    }

    .project-card>div:nth-of-type(2) {
        width: 98%;
        padding: 6px;
        border-radius: 5px;
    }

    .projectSkills {
        width: 30px;
    }
}